import { useNavigate } from "react-router";
import Back from "../../components/Common/Back";
import { Border } from "../../components/Common/Border";

const Edit = () => {
  const navigate = useNavigate();

  const move = (router) => {
    navigate(router);
  };

  const editProps = [
    {
      name: " ",
      router: 'profile'
    },
    {
      name: " ",
      router: 'saveVideo'
    },
    {
      name: " ",
      router: 'deleteVideo'
    },
    {
      name: " ",
      router: 'subChannel'
    },
  ];

  return (
    <div className="flex-col justify-between w-full h-screen"> {/* h-screen     */}
      <div className="flex items-center p-4 h-16 space-x-3 font-bold"> {/* h-16   */}
        <Back />
        <p className="mb-0"></p> 
      </div>

      <p 
        className="px-4"
        onClick={() => move('my')}
        > </p>
      <Border />
      {editProps.map((item) => (
        <p
          key={item.router} //     
          className="px-4"
          onClick={() => move(item.router)} //      
        >
          {item.name}
        </p>
      ))}
      <div className="flex justify-center mt-[200px] mb-4"> {/*    */}
        <button 
          className="bg-[#2A68E14D] bg-opacity-30 w-[80%] py-3 rounded-xl">
          Primiun  
        </button>
      </div>
    </div>
  );
};

export default Edit;
